import React, { useState, useEffect } from "react";
import {
    Container,
    Typography,
    Divider,
    Table,
    TableHead,
    TableRow,
    TableCell,
    TableBody,
    IconButton,
} from "@material-ui/core";
import { ArrowBack } from "@material-ui/icons";
import { connect } from "react-redux";
import moment from "moment";
import { withRouter } from "react-router-dom";
import api from "../../api/api";
import { logout } from "../../actions";

const ProductOrder = (props) => {
    const { logout, history } = props;
    const [productOrder, setProductOrder] = useState([]);

    useEffect(() => {
        document.title = "Product Order";
        fetchProductOrders();
    }, []);

    const fetchProductOrders = async () => {
        if (localStorage.getItem("mom-admin")) {
            try {
                const response = await api.get(`/api/product-order`, {
                    headers: {
                        Authorization: "Bearer " + localStorage.getItem("mom-admin"),
                    },
                });
                return setProductOrder(response.data.data);
            } catch (err) {
                return;
            }
        } else {
            return logout();
        }
    };

    const renderList = () => {
        if (productOrder.length) {
            return (
                <Table size="small">
                    <TableHead>
                        <TableRow>
                            <TableCell>#</TableCell>
                            <TableCell>Product</TableCell>
                            <TableCell>Customer</TableCell>
                            <TableCell>Contact (Customer)</TableCell>
                            <TableCell>Owner</TableCell>
                            <TableCell align="center">QTY</TableCell>
                            <TableCell align="center">Price</TableCell>
                            <TableCell align="center">Status</TableCell>
                            <TableCell>Order Date</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {productOrder.map((a, index) => {
                            return (
                                <TableRow key={index}>
                                    <TableCell>
                                        <Typography>{index + 1}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography>{a.name}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography>{a.cname}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography>{a.phone}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography>{a.mname}</Typography>
                                    </TableCell>
                                    <TableCell align="center">
                                        <Typography>{a.type === 1 ? "-" : a.quantity}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        <Typography>{a.price}</Typography>
                                    </TableCell>
                                    <TableCell>
                                        {
                                            a.status && <Typography align="center" style={{ color: a.status === 1 ? '#fe7517' : a.status === 2 ? '#07800b' : a.status === 3 ? '#07800b' : "#07800b" }}>{a.status === 1 ? 'Pending' : a.status === 2 ? 'Confirmed' : a.status === 3 ? 'Delivered' : null}</Typography>
                                        }
                                    </TableCell>
                                    <TableCell>
                                        <Typography>
                                            {moment(a.created).format("DD-MM-YYYY")}
                                        </Typography>
                                    </TableCell>
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            );
        } else {
            return (
                <Typography align="center" variant="h6" style={{ marginTop: 100 }}>
                    There is no product order yet
                </Typography>
            );
        }
    };

    return (
        <Container style={{ marginBottom: 380 }}>
            <Typography variant="h6">
                <IconButton
                    edge="start"
                    onClick={() => {
                        history.goBack();
                    }}
                >
                    <ArrowBack />
                </IconButton>
        Product Orders
      </Typography>
            <Divider style={{ marginTop: 20 }} />
            {renderList()}
        </Container>
    );
};


export default connect(null, { logout })(withRouter(ProductOrder));
